import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import { Title } from "../components/title"


import "../templates/index.scss"

const AboutPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="About" />
      <Title title="About me" />
      <p className="blog_post"> Whoever reads this, I welcome you!</p>

      <p className=" blog_post">I was not sure what to write here.
      At first when i wrote this page I intended to give some introduction on what i worked on in past
      but it made me sound cocky (you can allways visit my linkedin if you are interested). And at the end I decided
      to write short personal bio</p>
      <p className="blog_post">
        And it goes something like:
        My full name is Dragan Rakita, i was born and I am currently living in Novi Sad, Serbia. And i am owner and sole
        employer of Blue Alloy (hence the name of blog). Software engineer by life choice. Living alone without even a cat,
        playing games, reading and writing posts in hope somebody will find it interesting to read.
            </p>
      <p className="blog_post"> If you wish, you can contact me on email : dragan0rakita@gmail.com <br />
                 Or add me on:
            <ul>
          <li>twitter: @rakitadragan</li>
          <li>linkedin: https://www.linkedin.com/in/draganrakita/a</li>
          <li>github: https://github.com/rakita</li>
        </ul>
      </p>
    </Layout>
  )
}

export default AboutPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
